.pagination {
  align-items: center;
  display: flex;
  justify-content: flex-end;
  position: relative;
  margin-top: 24px;

  padding-right: 15px;
  width: 100%;
}

.pagination .frame {
  height: 26px;
  position: relative;
  width: 26px;

  color: #333;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 4px;
  display: inline-flex;
  margin-right: 5px;
  color: black;
  font-family: Barlow;
  padding-left: 5px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  padding: 4px;
}

.pagination .frame.active {
  background-color: #1e2528;
  color: #fff;
}

.pagination button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 14px;
  color: #007bff;
  margin-right: 10px;
  outline: none;
}

.pagination button:hover {
  text-decoration: underline;
}

.pagination .element-wrapper {
  align-items: center;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  gap: 10px;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

.elementWrapperLeftArrow {
  color: #d2d3d4;
}
.elementWrapperRightArrow {
  display: flex;
  width: 18px;
  height: 18px;
  padding: 2px 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #d2d3d4;

  gap: 10px;
  flex-shrink: 0;
}
.elementWrapperRightArrow:hover {
  display: flex;
  width: 18px;
  height: 18px;
  border-radius: 5px;
  padding: 8px;
  background-color: #e8e9e9;
  flex-direction: column;
  color: #1e2528;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-shrink: 0;
}
